import { Link } from "react-router-dom";
import EventoWrapper from "../components/eventos/EventoWrapper";
import { VscAdd } from "react-icons/vsc";
import { useState, useEffect, Fragment } from 'react';
import { collection, getFirestore, getDocs } from "firebase/firestore";
import MyFirebase from "../database/firebase";
import Loader from '../components/UI/Loader';
import useUser from "../hooks/use-user";
const app = MyFirebase();

const Homepage = props => {
    const [eventos, setEventos] = useState()
    const [isLoading, setIsloading] = useState(true)
    const db = getFirestore(app);

    const getEventos = async ()=> {
        const eventoRef = collection(db, "eventos");
        getDocs(eventoRef).then((querySnapshot) => {
            const loadedEventos = [];
            querySnapshot.forEach((doc) => {
                if (doc.data().users.includes(localStorage.getItem('uid'))) {
                    loadedEventos.push(doc.data());
                }
            });
            setEventos(loadedEventos)
            setIsloading(false)
        })
    }

    useEffect(() => {
        getEventos();
    }, [])

    return (
        <Fragment>
        {isLoading
         ? <div className="loader-wrapper"><Loader /></div>
         : <section className="padding gapper">
            <h2>EVENTOS PENDIENTES</h2>
            <EventoWrapper eventos={eventos}/>
            {!localStorage.getItem('uid') && <p><Link to='/register'>Reg√≠strate o Entra</Link> para ver tus eventos.</p>}
            <Link to='/crear-evento' className="crear-evento"><VscAdd /></Link>
          </section>
        }
         </Fragment>
       
    )
}

export default Homepage;